:root{
    --PrimaryColor:hsl(199, 100%, 33%);
    --SecondaryColor:hsl(187,85%,43%);
    --gradientColor:linear-gradient(to right,hsl(187,85%,43%)
     ,hsl(199,100%,33%));
    --whiteColor:hsl(0,0%,100%);
    --blackColor:hsl(201,33%,16%);
    --textColor:hsl(240,4%,36%);
    --whiteColorDream:hsl(0,0%,93%);
    --greyText:rgb(190,190,190);
    --inputColor:rgb(239,239,239);
    --bodyColor:rgb(240,240,240);
    --cardBG:rgb(225,225,235);
  }
  .home{
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    
    .overlay{
      position: absolute;
      height: 100%;
      width: 100%;
      background:rgb(47,106,127);
      top: 0;
      bottom: 0;
      z-index: 1;
      mix-blend-mode:multiply;
    }
    .video{
      position: absolute;
      width: 100%;
      height:100%;
      top: 0;
      bottom: 0;
      object-fit:cover;
    }
    .homeContent{
      width:100%;
      height: max-content;
      padding: 8rem 1.5rem 3rem;
      display: flex;
      row-gap: 3rem;
      flex-direction: column;
      align-items: stretch;
      justify-content: center;
      margin: auto;
      color: var(--whiteColor);
      z-index: 100;
      
      .textDiv{
        padding: 2rem 0;
  
        .smallText{
          font-size: 13px;
          text-transform: uppercase;
        }
        .homeTitle{
          position: absolute;
          font-size: 1.8rem;
        }
      }
    }
  }
  
  